import { drawKeypoints, drawPoint, drawPath } from './drawing.js';
import { findSimilar, buildVPTree } from './findSimilarVideo';

let normalisedPoses = [];
const poseData = [];
//  in-memory canvas to transfor hand img
const mCanvas=document.createElement('canvas');
mCanvas.width=300;
mCanvas.height=300;
const memoryContext=mCanvas.getContext('2d');

export async function buildImagesData(model, config) {

   const images = document.querySelector('#images').children;
   
   const imagesAndPoses = await normaliseImages(images, model, config);
   buildVPTree(normalisedPoses);

   return imagesAndPoses;
}

export async function findMatch (lookupPose) {
   const similarIndexOrder = await findSimilar(lookupPose);
   return similarIndexOrder
}

async function normaliseImages(images, model, config){
   
   for(let [i, img] of [...images].entries()){

      // get prediction
		const predictions = await model.estimateHands(img);

      if(predictions[0] && predictions[0].landmarks) {
         //set c
         // const centroid = getCentroid(
         //    predictions[0].landmarks[0],
         //    predictions[0].landmarks[2],
         //    predictions[0].landmarks[5],
         //    predictions[0].landmarks[9],
         //    predictions[0].landmarks[13],
         //    predictions[0].landmarks[17],
         //    )
         //set a
         // const centroid = getCentroid(
         //    predictions[0].landmarks[0],
         //    predictions[0].landmarks[4],
         //    predictions[0].landmarks[8],
         //    predictions[0].landmarks[12],
         //    predictions[0].landmarks[16],
         //    predictions[0].landmarks[20],
         //    )
         // set b
         const centroid = getCentroid(
            predictions[0].landmarks[0],
            predictions[0].landmarks[2],
            predictions[0].landmarks[5],
            predictions[0].landmarks[17])

         normalisedPoses.push(mapLandmarks(predictions[0].landmarks))
         predictions[0].centroid = centroid;
         
         // draw to canvas
         // get canvas and draw
         const canvas = document.querySelector(`#canvas-${img.getAttribute('id')}`);
         drawToCanvas(canvas, predictions, img, config.drawHandPoseOnData);
         poseData.push([canvas, predictions[0]]);
         const context = canvas.getContext('2d');
         
         
         if(config.drawHandPoseOnData){
            drawPoint(context, centroid[1], centroid[0], 4)
         
            drawPath(context, [predictions[0].landmarks[0], centroid],false)
         }
         

         // canvas.boundingBox =  predictions[0].boundingBox
         // handFoundPipeline(predictions, img)
      }
      
   }
   return poseData;
}

function copyImgFromCanvasToCanvas(canvasA, canvasB, topLeft, bottomRight){
   
   var originContext = canvasA.getContext('2d');
   var newContext = canvasB.getContext('2d');

   let [sx, sy] = topLeft;
   let [ex, ey] = bottomRight;
   const wx = ex - sx 
   const wy = ey - sy 

   // get img data
   const imgData = originContext.getImageData(sx, sy, wx, wy);
   newContext.putImageData(imgData, sx, sy);
}

// draw the keypoints if hter and img from img tag
function drawToCanvas(canvas, predictions, img, drawHandPoseOnData) {
   var context = canvas.getContext('2d');
   
   if (img)
      context.drawImage(
         img, 
         0,0
          );
      // context.drawImage(
      //    img, 
      //    predictions[0].boundingBox.topLeft[0], 
      //    predictions[0].boundingBox.topLeft[1],
      //    predictions[0].boundingBox.bottomRight[0] - predictions[0].boundingBox.topLeft[0],
      //    predictions[0].boundingBox.bottomRight[1] - predictions[0].boundingBox.topLeft[1]
      //     );

   if (predictions.length > 0 && drawHandPoseOnData) {
     const result = predictions[0].landmarks;
     drawKeypoints(context, result, predictions[0].annotations);
     context.beginPath();
     context.lineWidth = "2";
     context.strokeStyle = "lightgreen";

     context.rect(
        predictions[0].boundingBox.topLeft[0] > 0 ? predictions[0].boundingBox.topLeft[0] : 0,
        predictions[0].boundingBox.topLeft[1] > 0 ? predictions[0].boundingBox.topLeft[1] : 0,
        predictions[0].boundingBox.bottomRight[0] - predictions[0].boundingBox.topLeft[0] < 300 ? predictions[0].boundingBox.bottomRight[0] - predictions[0].boundingBox.topLeft[0] : 300,
        predictions[0].boundingBox.bottomRight[1] - predictions[0].boundingBox.topLeft[1] < 300 ? predictions[0].boundingBox.bottomRight[1] - predictions[0].boundingBox.topLeft[1] : 300);

     context.stroke();
   }
}

export function mapLandmarks(landmarks) {
   
      return landmarks.map(pos => [pos[0], pos[1]]).flat();
}

function getAngle(inputBone, matchBone) {

   // const angel = Math.tan(Math.abs(matchSlope - inputSlope / 1 + (matchSlope * matchSlope)))
   const dAx = inputBone[1][0] - inputBone[0][0];
   const dAy = inputBone[1][1] - inputBone[0][1];
   const dBx = matchBone[1][0] - matchBone[0][0];
   const dBy = matchBone[1][1] - matchBone[0][1];
   
   let angle = Math.atan2(dAx * dBy - dAy * dBx, dAx * dBx + dAy * dBy);
   // if(angle < 0) {angle = angle * -1;}
   return angle 
}
export async function placeImage(ctx, prediction, matchIndex, options){
   
   // drawPoint(ctx, prediction.centroid[1], prediction.centroid[0], 4)

   const userBone = [prediction.landmarks[0], prediction.centroid]
   const matchBone = [poseData[matchIndex][1].landmarks[0], poseData[matchIndex][1].centroid]

   const translateRotateX =  prediction.landmarks[0][0] ;
   const translateRotateY = prediction.landmarks[0][1] ;
   const translateMatchRootX =  - poseData[matchIndex][1].landmarks[0][0];
   const translateMatchRootY = - poseData[matchIndex][1].landmarks[0][1];
   const angel = getAngle(userBone, matchBone);

   const userDistance = Math.sqrt(Math.pow(prediction.centroid[0] - prediction.landmarks[0][0],2) + Math.pow(prediction.centroid[1] - prediction.landmarks[0][1],2))
   const matchDistance = Math.sqrt(Math.pow(poseData[matchIndex][1].centroid[0] - poseData[matchIndex][1].landmarks[0][0],2) + Math.pow(poseData[matchIndex][1].centroid[1] - poseData[matchIndex][1].landmarks[0][1],2))

   const scale = userDistance / matchDistance;
   // draw im to mem canvas
   // memoryContext.drawImage(poseData[matchIndex][0],0,0);

   // const originContext = poseData[matchIndex][0].getContext('2d');
   ctx.translate(translateRotateX, translateRotateY)
   ctx.rotate(-angel)
   ctx.translate(translateMatchRootX * scale, translateMatchRootY * scale)
   // ctx.globalAlpha = 0.65;
   // (default source over)
   if( options.overlay ) ctx.globalCompositeOperation = 'overlay';
   if( options.lighter ) ctx.globalCompositeOperation = 'lighter';
   if( options.multiply ) ctx.globalCompositeOperation = 'multiply';
   if( options.screen ) ctx.globalCompositeOperation = 'screen';
   if( options.softLight ) ctx.globalCompositeOperation = 'soft-light';
   if( options.difference ) ctx.globalCompositeOperation = 'difference';

   // drawPoint(ctx,0,0,5)
   ctx.drawImage(
      poseData[matchIndex][0],
      0,
      0,
      (poseData[matchIndex][0].width ) * scale ,(poseData[matchIndex][0].height) * scale );

   // ctx.drawImage(
   //    poseData[matchIndex][0],
   //    poseData[matchIndex][1].boundingBox.topLeft[0],
   //    poseData[matchIndex][1].boundingBox.topLeft[1],
   //    (poseData[matchIndex][1].boundingBox.bottomRight[0] - poseData[matchIndex][1].boundingBox.topLeft[0]),
   //    (poseData[matchIndex][1].boundingBox.bottomRight[1] - poseData[matchIndex][1].boundingBox.topLeft[1]),
   //    translateMatchRootX * scale,
   //    translateMatchRootX * scale,
   //    (poseData[matchIndex][1].boundingBox.bottomRight[0] - poseData[matchIndex][1].boundingBox.topLeft[0]) * scale,
   //    (poseData[matchIndex][1].boundingBox.bottomRight[1] - poseData[matchIndex][1].boundingBox.topLeft[1]) * scale

   //    );
   ctx.globalCompositeOperation = 'source-over';

   ctx.translate(-translateMatchRootX * scale, -translateMatchRootY * scale)
   ctx.rotate(angel)
   ctx.translate(-translateRotateX, -translateRotateY)
   return;
}

export function getCentroid(...args) {
   const [sumX, sumY] = args
      .reduce((sums, point) => [sums[0]+ point[0], sums[1]+ point[1]], [0,0]);
   
   return [sumX / args.length, sumY / args.length]
}