import * as handpose from "@tensorflow-models/handpose";
import * as utils from "./utils";
import { drawKeypoints, drawPoint, drawPath } from "./utils/drawing.js";
import { loadVideo } from "./utils/video.js";
import { buildImagesData,  mapLandmarks, placeImage, getCentroid } from "./utils/imagesData";
import { findSimilar } from "./utils/findSimilarVideo";

let videoWidth, videoHeight;
const VIDEO_WIDTH = 640;
const VIDEO_HEIGHT = 360;

const mobile = utils.isMobile();

const state = {};
state.confidence = 0.5;
state.flip = false;
state.drawInputPose = true;
state.drawHandPoseOnData = false
state.opacity = 0.6
state.drawDirection = true;
// (overlay transformations)
state.destinationIn = false
state.lighter = false;
state.screen = false;
state.softLight
= false;
state.multiply = false;
state.overlay = false;
state.difference = false;


let model;
let imagesAndPoses;
let globalCtx;

let realtimeAnimationFrameRef;

const main = async () => {
	model = await handpose.load({ detectionConfidence: state.confidence });
   
   // build hand pool data
   imagesAndPoses = await buildImagesData(model, state);

   let video;

	try {
		video = await loadVideo(mobile);
	} catch (e) {
		noCameraMessage(e);
	}

	landmarksRealTime(video);
};

function noCameraMessage(e) {
	let info = document.getElementById("info");
	info.textContent = e.message;
	info.style.display = "block";
	throw e;
}

function setupDatGui(state) {
   const gui = new dat.GUI();
   state.opacity = 0.6;
   state.drawDataPose = true;
   state.sourceRatio = 0.3;
   state.drawHandPose = true;
   if (globalCtx) globalCtx.globalAlpha = state.opacity;

   const handSource = gui.addFolder('pose estimation')

	//confidence bar
	handSource.add(state, "confidence", 0, 1).onChange(async (sliderValue) => {
		state.confidence = sliderValue;
		model = await handpose.load({ detectionConfidence: state.confidence });
   });

    // flip horizontally
    handSource.add(state, "flip").onChange((flip) => {
      state.flip = flip;
   });

   const video = gui.addFolder('video')
   
   	//global alpha
      video.add(state, "opacity", 0, 1).onChange((sliderValue) => {
		state.opacity = sliderValue;
      if (globalCtx) globalCtx.globalAlpha = sliderValue;
   });
   video.open()

   const drawing = gui.addFolder('drawing')
       // source image ratio diffrence
       drawing.add(state, "drawInputPose").onChange((val) => {
		state.drawInputPose = val;
   });
       // source image ratio diffrence
       drawing.add(state, "drawDirection").onChange((val) => {
         state.drawDirection = val;
   });
     // add hand
     drawing.add(state, "drawDataPose").onChange((flip) => {
      state.drawDataPose = flip;
   });
   

    // draw data pose switch
    drawing.add(state, "drawHandPoseOnData").onChange((val) => {
      state.drawHandPoseOnData = val;
      gui.destroy()
      cancelAnimationFrame(realtimeAnimationFrameRef)
      main();
   });
   drawing.open();
   const blending = gui.addFolder('blending');
   
   blending.add(state, "multiply").onChange(bool => {
      state.multiply = bool;
   })
 
   blending.add(state, "difference").onChange(bool => {
      state.difference = bool;
   })
     
   blending.add(state, "lighter").onChange(bool => {
      state.lighter = bool;
   })
   
   blending.add(state, "screen").onChange(bool => {
      state.screen = bool;
   })
   
   blending.add(state, "softLight").onChange(bool => {
      state.softLight = bool;
   })
   
  
   blending.add(state, "overlay").onChange(bool => {
      state.overlay = bool;
   })
   // blending.open();
   
}
const landmarksRealTime = async (video) => {
   // control
   setupDatGui(state);

   // stats (frame rate)
	const stats = new Stats();
	stats.showPanel(1);
	document.body.appendChild(stats.dom);

	videoWidth = video.videoWidth;
	videoHeight = video.videoHeight;

	const canvas = document.getElementById("output");

	canvas.width = videoWidth;
	canvas.height = videoHeight;

	const ctx = canvas.getContext("2d");
   globalCtx = ctx;
   globalCtx.globalAlpha = state.opacity;

	video.width = videoWidth;
	video.height = videoHeight;

	// ctx.clearRect(0, 0, videoWidth, videoHeight);
	// ctx.strokeStyle = "lightgreen";
	// ctx.fillStyle = "lightgreen";

	ctx.translate(canvas.width, 0);
	ctx.scale(-1, 1);
   // console.log(imagesAndPoses)

	async function frameLandmarks() {
      stats.begin();
		ctx.drawImage(video, 0, 0, videoWidth, videoHeight, 0, 0, canvas.width, canvas.height);
		const predictions = await model.estimateHands(video, state.flip);


		if (predictions.length > 0) {
         const centroid = getCentroid(predictions[0].landmarks[0],predictions[0].landmarks[2],predictions[0].landmarks[5],predictions[0].landmarks[17])
         
         if (state.drawDirection)
         {drawPoint(ctx, centroid[1], centroid[0], 4)
         ctx.strokeStyle = 'lightgreen'
         ctx.lineWidth = 3;
         drawPath(ctx, [predictions[0].landmarks[0], centroid],false)
         ctx.strokeStyle = 'black'
         ctx.lineWidth = 2;}

         predictions[0].centroid = centroid

         const result = predictions[0].landmarks;
         if( state.drawInputPose)
			   drawKeypoints(ctx, result, predictions[0].annotations);
         
         // overlay hand image
         const matchIndex = findSimilar(mapLandmarks(predictions[0].landmarks));
         if (state.drawDataPose)
            await placeImage(ctx, predictions[0], matchIndex, state);

      }
		stats.end();
		realtimeAnimationFrameRef = requestAnimationFrame(frameLandmarks);
	}

	frameLandmarks();

};

navigator.getUserMedia =
	navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia;

main();
